.spanmenucus {
  text-align: center;
  color: white;
  font-size: 13px;
  white-space: nowrap;
}
.spanmenucus {
  position: absolute;
  bottom: -5px; /* ปรับตำแหน่งจากด้านล่าง */
  left: 50%;
  transform: translateX(-50%); /* จัดให้อยู่กึ่งกลางแนวนอน */
  width: max-content; /* ให้ความกว้างพอดีกับข้อความ */
  text-align: center;
  color: white;
  font-size: 11px;
  white-space: nowrap; /* ป้องกันการขึ้นบรรทัดใหม่ */
  margin: 0;
  padding: 0;
}

.imgmenucus {
  height: 40px !important;
  margin-bottom: -15px;
  margin-top: 5px;

  position: relative;
}

.menucus {
  z-index: 90;
  margin: 0;
  display: flex;
  width: 100%;
  font-size: 1.5em;
  padding: 0px;
  position: fixed;
  bottom: 0px;
  align-items: center;
  justify-content: center;
  background: linear-gradient(0deg, #02388c 0%, #0691ff 100%);
  background-size: cover;
  box-shadow: 0px -1px 6px #008151;
}
.menucus__item {
  all: unset;
  flex-grow: 1;
  z-index: 100;
  display: flex;
  cursor: pointer;
  position: relative;
  border-radius: 50%;
  align-items: center;
  will-change: transform;
  justify-content: center;
  transition: transform var(--timeOut, var(--duration));
  height: 60px;
}

.menucus__item::before {
  content: "";
  z-index: -1;
  width: 4.2em;
  height: 4.2em;
  border-radius: 50%;
  position: absolute;
  transform: scale(0);
  transition: background-color var(--duration), transform var(--duration);
}

.menucus__item.active {
  transform: translate3d(0, -0.8em, 0);
}

.menucus__item.active::before {
  transform: scale(1);
  background-color: #00358287;
}

.menucus__item .icon {
  width: 50px;
  /* stroke: white; */
  color: white;
  fill: transparent;
  stroke-linecap: round;
  text-align: center;
  stroke-linejoin: round;
  stroke-dasharray: 400;
  height: 55px;
  position: relative;
  margin-bottom: 8px;
}
.menucus__item:nth-child(3) .icon {
  width: 40px;
  height: 65px;
}
.menucus__item:nth-child(3) .spanmenucus {
  bottom: 0px;
}
.menucus__item.active:nth-child(3) .spanmenucus {
  bottom: -5px;
}

.menucus__item.active .icon {
  animation: strok 1.5s reverse;
}

@keyframes strok {
  100% {
    stroke-dashoffset: 400;
  }
}

.menucus__border {
  left: 0;
  z-index: 91;
  bottom: 97%;
  width: 10.9em;
  height: 2.4em;
  position: absolute;
  clip-path: url(#menucus);
  will-change: transform;
  background: linear-gradient(0deg, #0691ff 0%, #85c8ff 100%);
  /* Important: Remove the transition property to prevent interference */
  transition: none;
}

/* Animation classes for sliding effect */
.menucus__border.slide-right,
.menucus__border.slide-left {
  animation: slide 0.8s cubic-bezier(0.645, 0.045, 0.355, 1) forwards;
}
@keyframes slide {
  from {
    transform: translate3d(var(--prev-position), 0, 0);
  }
  to {
    transform: translate3d(var(--new-position), 0, 0);
  }
}

/* Enhanced menu item transitions */
.menucus__item {
  transition: transform 0.8s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.menucus__item.active {
  transform: translate3d(0, -0.8em, 0);
}
.svg-container {
  width: 0;
  height: 0;
}

@media only screen and (max-width: 800px) {
  .menucus__item::before {
    width: 6.2em;
    height: 6.2em;
  }

  .spanmenucus {
    font-size: 12px;
    text-shadow: 0px 1px 3px black;
  }

  .imgmenucus {
    margin-bottom: -4px;
  }
}

@media screen and (max-width: 50em) {
  .menucus {
    font-size: 0.8em;
  }
}

@media only screen and (min-width: 900px) {
  .menucus {
    display: none;
  }
}
