@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css?family=Open+Sans");
@import url("https://fonts.googleapis.com/css2?family=Kanit:wght@200&display=swap");
@import url("https://fonts.googleapis.com/css?family=Sarabun");

html,
body,
a,
label,
h1,
h2,
h3,
h4,
h5,
p,
span {
  font-family: "Sarabun", sans-serif !important;
  /* font-weight: 900;*/
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.alert_table {
  font-family: "sarabun";
  border-collapse: collapse;
  width: 100%;
  color: #000000;
}

.alert_table td,
.alert_table th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  font-weight: 400;
}

.alert_table tr:nth-child(even) {
  background-color: #ffffff;
}
div:where(.swal2-container) .swal2-html-container {
  margin: 0px !important;
}
.deposit_page .ant-segmented .ant-segmented-item-selected {
  background-color: #0167FF;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.03),
    0 1px 6px -1px rgba(0, 0, 0, 0.02), 0 2px 4px 0 rgba(0, 0, 0, 0.02);
  color: rgb(255, 255, 255);
}
.btn-home {
  background-image: linear-gradient(144deg, #0d62e2, #018dff 50%, #5597fa);

  box-shadow: rgba(43, 39, 70, 0.2) 0 15px 30px -5px;
  box-sizing: border-box;
  transition: 5ms;
}

.btn_home1 {
  background-color: #01a5ff;
  background-color: #0167ff;


}

.btn_home2 {
  background-color: #1dba61;

}

.btn_home3 {
  background-color: #ffb30f;
  
}
.btn_home1 img,.btn_home2 img,.btn_home3 img{
  filter: drop-shadow(1px 1px 0px #2b2b2b);
}
.btn_home1 p,.btn_home2 p,.btn_home3 p{
  text-shadow: 0px 1px 2px black;
}
.btn_home1,.btn_home2,.btn_home3{
  box-shadow: #57575771 0 2px 5px ;
}

.btn_home1:active,
.btn_home1:hover,
.btn_home2:active,
.btn_home2:hover ,
.btn_home3:active,
.btn_home3:hover {
  border: 2px solid #ffffff;
}

.btn-home:active,
.btn-home:hover {
  filter: saturate(2);
  border: 1px solid #dddddd;
}
.btn-home2 {
  background-image: linear-gradient(343deg, #fbac30, #5d46dd 50%, #5d8edc);
  box-shadow: rgba(43, 39, 70, 0.2) 0 15px 30px -5px;
  box-sizing: border-box;
  transition: 5ms;
}

.btn-home2:active,
.btn-home2:hover {
  filter: saturate(1.2);
  border: 1px solid #a8a8a8;
}
.white_text .MuiPaginationItem-page,
.white_text .MuiPaginationItem-icon {
  /* color: #000000 !important; */
}

.text-hover-show {
  width: 550px;

  text-overflow: ellipsis;
  cursor: pointer;
  word-break: break-all;
  overflow: hidden;
  white-space: nowrap;
}
@media only screen and (max-width: 800px) {
  .text-hover-show {
    width: 200px;
  }
}
.text-hover-show-admin {
  width: 100%;

  text-overflow: ellipsis;
  cursor: pointer;
  word-break: break-all;
  overflow: hidden;
  white-space: nowrap;
}

.text-hover-show:hover,
.text-hover-show-admin:hover {
  overflow: visible;
  white-space: normal;
  height: auto; /* just added this line */
  text-wrap: wrap;
}
.text-shadow-1 {
  text-shadow: 0px 1px 2px #000;
}
.text-shadow-2 {
  text-shadow: 0px 1px 1px #000;
}
.text-shadow-3 {
  text-shadow: 0px 1px 3px #202020;
}
.text-shadow-4 {
  text-shadow: 0px 0px 3px #ffffff;
}

::-webkit-scrollbar {
  height: 4px;
  width: 4px;
  background: gray;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

::-webkit-scrollbar-thumb:horizontal {
  background: #000;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:vertical {
  background: #0055f3;
  border-radius: 10px;
}

.gecko-static-group {
  display: flex;
  justify-content: center !important;
}


.toggle-password {
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  width: 35px !important;
  height: 35px !important;

  display: flex;
  align-items: center;
  justify-content: center;

  -webkit-tap-highlight-color: transparent;
}

.toggle-password svg {
  width: 24px !important;
  height: 24px !important;
  stroke-width: 2;
  color: #555;
}

@supports (-webkit-touch-callout: none) {
  .toggle-password svg {
      width: 24px !important;
      height: 24px !important;
      min-width: 24px !important;
      min-height: 24px !important;
      /* เพิ่ม GPU acceleration */
      transform: translateZ(0);
      -webkit-transform: translateZ(0);
      /* ป้องกันการ scale down */
      flex-shrink: 0;
      /* บังคับให้แสดงขนาดจริง */
      max-width: none;
      max-height: none;
  }
}

